<template>
<u-linear-layout>
    <u-button @click="alert()">Alert</u-button>
    <u-button @click="confirm()">Confirm</u-button>
</u-linear-layout>
</template>

<script>
export default {
    methods: {
        alert() {
            this.$alert('');
        },
        confirm() {
            this.$confirm('').then(() => {
                console.info('');
            }).catch(() => {
                console.info('');
            });
        },
    },
};
</script>



